//БЕКДРОП

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 1;

  //сюди ще піде транзішн переходу, як варіант внизу
  opacity: 1;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

// сюди пишемо стан бекдроп коли приховано бекдроп
// .backdrop__modal-map.is-hidden {
//   opacity: 0;
//   pointer-events: none;
// }

//МОДАЛЬНЕ ВІКНО

.modal {
  position: absolute;

  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1);

  width: 280px;
  background-color: $txt-white;
  padding: 28px;

  border: 2px solid $dyellow-color;
  border-radius: 10px;

  opacity: 1;

  //сюди ще піде транзішн переходу, як варіант внизу
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1),
    opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

// сюди пишемо стан модалки коли приховано бекдроп
// .backdrop__modal-map.is-hidden .modal {
//   transform: translate(-50%, -50%) scale(0.9);
//   opacity: 0;
// }

@media screen and (min-width: 768px) {
  .modal {
    width: 568px;
    padding: 32px 60px;
  }
}

//КНОПКА ЗАКРИТТЯ

.modal__btn-close {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 12px;
  right: 12px;
  width: 20px;
  height: 20px;
  border: 0;
  padding: 0;
  background-color: transparent;
  color: $txt-black;
}

//ЗАГОЛОВОК МОДАЛЬНОГО ВІКНА

.modal__header {
  font-family: 'Seymour One';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;

  text-align: center;

  color: $txt-black;

  margin-bottom: 20px;
}

@media screen and (min-width: 768px) {
  .modal-map__header {
    font-size: 20px;
    line-height: 1.4;
    margin-bottom: 32px;
  }
}

//КНОПКА ЧИ ПОСИЛАННЯ

.modal__btn {
  display: flex;
  margin-left: auto;
  margin-right: auto;
  margin-top: 20px;

  padding: 12px 34px;
  background-color: $dyellow-color;
  border-radius: 10px;
  border-width: 0;

  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: calc(15 / 12);
  text-align: center;

  color: $txt-black;
}

@media screen and (min-width: 768px) {
  .modal__btn {
    margin-top: 32px;

    font-size: 14px;
    line-height: calc(17 / 14);
  }
}
